<!-- article.vue -->
<template>
	<div class="content-wrapper">
		<content-header v-bind:menuList="menuList"></content-header>
		<section class="content">
			<img src="../img/get_file_1.png" class="img-responsive">
    	</section>
	</div>
</template>
<script>
  var md = require("markdown-it")();
  import contentHeader from './content_header'
  
  export default{
    data(){
      return {
        menuList:[
          {
          	text:"article",
            isActive:"false",
            url:""
          }
        ]
      }
    },
    components:{
      contentHeader
    },
    methods:{

    },
    mounted(){
    	var file = require("../md/"+this.$route.params.fileName+".md");
    	this.menuList.push({
    		text:this.$route.params.fileName,
    		isActive:"true",
    		url:""
    	})
    	$.get(file).success(text=>{
    		console.log(text)
    		$(".content").html(md.render(text).replace(/%img_01%/,"<img src='/static/img/get_file_1.png' class='img-responsive' />"))
    	})
    }
  }
</script>


