<!-- http.vue -->
<template>
	<div class="content-wrapper">
		<content-header v-bind:menuList="menuList"></content-header>
		<section class="content">
			<h4>Http相关问题</h4>
      		<ul>
		        <li>
		        	<router-link :to="{name:'article',params:{fileName:'$get_file'}	}">
			        	前端巧用$.get()将markdown文件内容读取，并直接显示再页面上
			        </router-link>
		        </li>
		        <li>
		        	<router-link :to="{name:'article',params:{fileName:'upload_file'}	}">
			        	使用formidable做node的文件上传，并利用iframe让表单提交不跳转
			        </router-link>
		        </li>
	        </ul>
    	</section>
	</div>
</template>

<script>
  import contentHeader from './content_header'
  export default{
    data(){
      return {
        menuList:[
          {
            text:"Http",
            isActive:"true",
            url:"http"
          }
        ]
      }
    },
    components:{
      contentHeader
    }
  }
</script>



